<template>
  <div class="w-[2000px] border p-4 relative">
    <div class="flex flex-wrap gap-0.5 border p-4 w-full mt-24">
      <div
        v-for="(item, index) in 500"
        :key="index"
        :id="'box-' + index"
        class="w-10 h-10 bg-red-500 flex items-center justify-center text-white"
      >
       
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      startBox: null, // Simpan ID kotak awal saat drag
    };
  },
  methods: {
    appendBox(size, color) {
      let box0 = this.$el.querySelector('#box-0');
      let boxN = this.$el.querySelector(`#box-${size * 47}`);

      let box0rect = box0.getBoundingClientRect();
      let boxNrect = boxN.getBoundingClientRect();

      let width = boxNrect.right - box0rect.left;
      let height = boxNrect.bottom - box0rect.top;
      let left = box0rect.left + window.scrollX;
      let top = box0rect.top + window.scrollY;

      let newBox = document.createElement('div');
      newBox.style.width = width + 'px';
      newBox.style.height = height + 'px';
      newBox.style.position = 'absolute';
      newBox.style.left = left + 'px';
      newBox.style.top = top + 'px';
      newBox.style.backgroundColor = color;
      newBox.style.opacity = '0.6';
      newBox.style.cursor = 'grab';
      newBox.style.border = `2px solid ${color.replace('rgba', 'rgb').split(', 0.6')[0]}`;
      newBox.style.zIndex = "1000";
      document.body.appendChild(newBox);

      this.makeDraggable(newBox, size);
    },

    makeDraggable(el, size) {
      let isDragging = false;
      let offsetX = 0, offsetY = 0;

      el.addEventListener('mousedown', (e) => {
        isDragging = true;
        offsetX = e.clientX - el.getBoundingClientRect().left;
        offsetY = e.clientY - el.getBoundingClientRect().top;
        el.style.cursor = "grabbing";

        // Temukan kotak merah di bawah mouse saat drag dimulai
        let startBox = document.elementFromPoint(e.clientX, e.clientY);
        if (startBox && startBox.id.startsWith('box-')) {
          this.startBox = startBox.id; // Simpan ID kotak awal
        }
      });

      document.addEventListener('mousemove', (e) => {
        if (!isDragging) return;
        el.style.left = e.clientX - offsetX + 'px';
        el.style.top = e.clientY - offsetY + 'px';
      });

      document.addEventListener('mouseup', (e) => {
        if (!isDragging) return;
        isDragging = false;
        el.style.cursor = "grab";

        
      });
    },

    snapToBox(el, targetBoxId, size) {
      let targetBox = document.getElementById(targetBoxId);
      if (!targetBox) return;

      let targetRect = targetBox.getBoundingClientRect();

      el.style.left = targetRect.left + window.scrollX + 'px';
      el.style.top = targetRect.top + window.scrollY + 'px';
    }
  },
  mounted() {
    this.appendBox(2, 'rgba(0, 255, 0, 0.6)');  // Kotak Hijau 2x2
    this.appendBox(3, 'rgba(255, 255, 0, 0.6)');  // Kotak Kuning 3x3
  }
};
</script>
